body {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  overflow: auto;
  overflow-x: hidden;
}

.conversation {
  background-color: rgb(0, 0, 0);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 1px;
  width: 100%;
  border: 1px solid black;
  overflow: hidden;
  margin: 20px 0;
  box-sizing: border-box;
}
.conversation .msg {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  border-radius: 2px;
  word-break: normal;
}
.conversation .msg-chat {
  background-color: white;
  padding: 0 10px;
}
.input {
  width: 99%;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 1px;
  overflow: hidden;
  border: 1px solid black;
  border-radius: 10px;
  margin-top: 50px;
  box-sizing: border-box;
  background-color: black;
}
.input input {
  background-color: white;
  text-transform: capitalize;
  outline: none;
  border: none;
  width: 100%;
  font-size: 15px;
  padding: 10px 20px;
  box-sizing: border-box;
}
.msg-container {
  width: 100vw;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.msg-container button {
  background-color: black;
  color: white;
  border: none;
  border-radius: 20px;
  padding: 5px 10px;
  text-shadow: 0 0 8px white, 0 0 8px white;
  box-shadow: 0 0 4px 0px black;
  cursor: pointer;
  margin: 0 5px 0 0;
}
.app {
  max-width: 500px;
  width: 95vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  box-sizing: border-box;
}
h1 {
  margin-top: 50px;
}
.create-templeate,
.use-templeate {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(38, 38, 38, 0.629);
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.create-templeate > div > div h1,
.use-templeate > div > div > h1 {
  margin: 0;
  padding: 0;
}
.create-templeate > div > div i {
  margin-top: 6px;
}
.create-templeate > div > div,
.use-templeate > div > div {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  margin: 30px 0 0 0;
  text-align: end;
}
.create-templeate > div,
.use-templeate > div {
  width: 400px;
  min-height: 350px;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  gap: 20px;
}

.create-templeate > div textarea {
  width: 90%;
  padding: 10px 10px;
  border-radius: 10px 0 0 10px;
  height: 100px;
  word-wrap: break-word;
  outline: none;
  border: 1px solid black;
  resize: none;
}

.create-templeate > div .btns {
  display: flex;
  gap: 10px;
}
.create-templeate > div .btns button {
  padding: 10px 20px;
  color: white;
  background-color: black;
  border: none;
  border-radius: 20px;
  outline: none;
  text-shadow: 0 0 8px white;
}
.use-templeate > div .context {
  padding: 10px;
  flex-direction: column;
  text-align: center;
}
.use-templeate > div .context button {
  color: white;
  background-color: black;
  padding: 10px 20px;
  border-radius: 20px;
  border: none;
  margin-bottom: 20px;
  cursor: pointer;
}
.use-templeate > div {
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 50%;
}
